<template>
    <el-container>
        <el-header>
            系统设置
        </el-header>
        <el-main>
            <el-card shadow="hover">
                <el-tabs v-model="tabsActive">
                    <el-tab-pane label="数据拟合" name="1">
                        <el-form label-width="20%">
                            <el-form-item label="阻止自动拟合">
                                <el-switch active-color="#F71B3A" @change="handleStopFittingChange" v-model="fit.stopFitting"></el-switch>
                            </el-form-item>
                            <el-form-item label="立即拟合">
                                <el-button @click="handleFitLevelNowCLick">仅拟合水位</el-button>
                                <el-button @click="handleFitAreaNowCLick">仅拟合面积</el-button>
                                <el-button @click="handleFitAllNowCLick">拟合水位与面积</el-button>
                            </el-form-item>
                        </el-form>
                    </el-tab-pane>

                </el-tabs>
            </el-card>

        </el-main>
    </el-container>
</template>

<script>
    export default {
        name: "setting",
        props: [],
        data: function () {
            return {
                tabsActive: '1',
                fit:{
                    stopFitting: false,
                }
            }
        },
        methods: {
            handleStopFittingChange: function () {
                if(this.fit.stopFitting){
                    this.axios.get('/api/setting/pauseFitting',{
                        params:{
                            method:'true',
                        }
                    }).then((response)=>{
                        if (response.status === 200){
                            this.$notify({
                                title: '关闭',
                                message: '系统自动拟合已关闭。',
                                type: 'success'
                            });
                        }
                        else {
                            this.$notify({
                                title: '失败',
                                message: '连接错误！',
                                type: 'error'
                            });
                        }
                    })
                }
                else {
                    this.axios.get('/api/setting/pauseFitting',{
                        params:{
                            method:'',
                        }
                    }).then((response)=>{
                        if (response.status === 200){
                            this.$notify({
                                title: '开启',
                                message: '系统自动拟合已开启。',
                                type: 'success'
                            });
                        }
                        else {
                            this.$notify({
                                title: '失败',
                                message: '连接错误！',
                                type: 'error'
                            });
                        }
                    })
                }
            },
            handleFitLevelNowCLick: function () {
                this.axios.get('/api/setting/fitLevelNow').then((response)=>{
                    if (response.status === 200){
                        this.$notify({
                            title: '成功',
                            message: '水位拟合完毕。',
                            type: 'success'
                        });
                    }
                    else {
                        this.$notify({
                            title: '失败',
                            message: '连接错误！',
                            type: 'error'
                        });
                    }
                })
            },
            handleFitAreaNowCLick: function () {
                this.axios.get('/api/setting/fitAreaNow').then((response)=>{
                    if (response.status === 200){
                        this.$notify({
                            title: '成功',
                            message: '水域面积拟合完毕。',
                            type: 'success'
                        });
                    }
                    else {
                        this.$notify({
                            title: '失败',
                            message: '连接错误！',
                            type: 'error'
                        });
                    }
                })
            },
            handleFitAllNowCLick: function () {
                this.axios.get('/api/setting/fitAllNow').then((response)=>{
                    if (response.status === 200){
                        this.$notify({
                            title: '成功',
                            message: '水位与面积拟合完毕。',
                            type: 'success'
                        });
                    }
                    else {
                        this.$notify({
                            title: '失败',
                            message: '连接错误！',
                            type: 'error'
                        });
                    }
                })
            }
        },
        mounted(){
            this.axios.get('/api/setting/getPauseFittingStatus').then((response)=>{
                if (response.status === 200){
                    this.fit.stopFitting = response.data;
                }
                else {
                    this.$notify({
                        title: '失败',
                        message: '连接错误！',
                        type: 'error'
                    });
                }
            })
        }
    }
</script>

<style scoped>
    .el-card {
        border: 1px solid #d4d4d4;
        width: 100%;
    }
</style>
